@font-face {
  font-family: 'iconfont';
  src: url('data:font/ttf;charset=utf-8;base64,')
    format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.iconclose:before {
  content: "\e600";
}

.iconseleted:before {
  content: "\e601";
}

